.main {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;

  padding-left: 15px;

  background-color: var(--color-main-bg);
  border-radius: 8px;

  overflow-y: scroll;
}

@media (max-width: 767px) {
  .main {
    order: 1;
    padding-left: 0;
  }
}

@media (max-width: 575px) {
  .main {
    max-width: 300%;
  }
}